<template>
  <div id="app">
    <header>
      <p>{{ hello }}</p>
    </header>
    <main>
      <router-view></router-view>
    </main>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      hello: 123
    }
  },
  components: {
    
  }
}
</script>

<style lang="scss">
* {
  margin: 0;
  padding: 0;
}

html, body, #app {
  width: 100%;
  height: 100%;
}

#app {
  display: flex;
  flex-direction: column;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #292f3c;
  color: white;
}

#app > main {
  flex: 1;
}
</style>



